﻿// Config
$nav-link-color: blue;
$nav-link-hover-color: blue;
$nav-link-text-color: white;
$nav-link-text-hover-color: white;

$nav-background-color: #ccc;

/*#region MENU Base */

.site-nav{
    
    width:100%;
    background-color:$nav-background-color;

    ul{
        @extend %display-flex;
        margin:0;
    }
    li{
        -ms-flex:1 1 auto;
        -webkit-box-flex:1;
        -webkit-flex:1 1 auto;
        flex:1 1 auto;
        position:relative;
    }

    a{
        @extend %display-flex;
        align-items: center;
        justify-content: center;
        padding:1rem;
        background-color:$nav-link-color;
        color:$nav-link-text-color;
        &:hover {
            color:$nav-link-text-hover-color;
            background-color:$nav-link-hover-color;
        }
    }
}




/*#endregion*/

