/* config */

$body-text-color: black;

/* styles */

html {
    font-size: 62.5%; // 1.0rem === 10px
    height: 100%;
    padding: 0;
}

body {
    position: relative;
    line-height: $base-line-height;
    font-family: $font-primary;
    color: $color-black;
    font-size:$base-font-size;
    height: 100%;
}