/* config */
$color-default-link: $color-blue;
$color-link-visited: black;
$unordered-bullet-color: black;

p {
    font-size: $base-font-size;
    margin-top: 0;
    margin-bottom: 1.5rem;
}

a {
    color: $color-default-link;
    text-decoration: underline;

    &:hover,
    &:before,
    &:after {
        text-decoration: none;
    }

    &:hover {
        text-decoration: none;
    }

    &:visited {
        color: $color-link-visited;
    }
}

ul, ol {
    list-style: none;
    padding: 0;
    margin: 0;
}

time{
    display: block;
    margin-bottom:1.5rem;
}

ul, ol, dl, dd,
blockquote, p, address,
hr,
table,
fieldset, figure,
pre {
    margin-bottom: $base-spacing-unit;
}