/*#region SETTINGS */
// Variables and base site settings used globally only.
@import "base/scss/settings/extendables";
@import "base/scss/settings/global";
@import "base/scss/settings/colors";

/*#endregion*/

/*#region TOOLS */
// Mixins and functions which are used globally.
/*@import "base/scss/tools/bourbon/bourbon";*/    /* Docs http://bourbon.io/ */
/*@import "base/scss/tools/susy/susy";*/          /* Docs http://susydocs.oddbird.net/en/latest/ */
/*@import "base/scss/tools/rocktime.mixins";*/    /* Additional mixins to add custom functions */
/*#endregion*/

/*#region GENERIC */
// General base styling. Not normally changed.
@import "base/scss/generic/normalize";
@import "base/scss/generic/box-sizing";
@import "base/scss/generic/reset";
@import "base/scss/generic/shared";
/*#endregion*/

/*#region ELEMENTS */
// Bare unclassed elements. No classes used.
@import "base/scss/elements/form";
@import "base/scss/elements/headings";
@import "base/scss/elements/images";
@import "base/scss/elements/page";
@import "base/scss/elements/typography";
/*#endregion*/

/*#region OBJECTS */
// Non-cosmetic design patterns. Classes used.
@import "base/scss/objects/wrappers";
@import "base/scss/objects/layout";
@import "base/scss/objects/media";
@import "base/scss/objects/error-messages";
/*#endregion*/

/*#region COMPONENTS */
// Pieces of user interface. Cosmetic design patterns.
/*#region Primary components - Will be seen first */

@import "components/site-header/site-header";
@import "components/logo/logo";
@import "components/site-footer/site-footer";
@import "components/site-navigation/site-navigation";
/*#endregion*/



/*#region TRUMPS */
// Overrides and heavy-handed styling.
@import "base/scss/trumps/links";
@import "base/scss/trumps/widths";
/*#endregion*/