/* config */

$color-h1: black;
$color-h2: black;
$color-h3: black;
$color-h4: black;
$color-h5: black;
$color-h6: black;
$header-underline-color: black;

h1, h2, h3, h4, h5 {
    font-family: $font-headings;
    font-weight: normal;
    line-height:1.2;
    margin-top: 0;
}

h1 {
    font-size: 3.0rem;
    color: $color-h1;
    margin: 1.6rem 0;
    &.underline{
        padding-bottom:1.0rem;
        border-bottom:1px solid $header-underline-color;
    }
}

h2 {
    font-size: 3.0rem;
    color: $color-h2;
    margin: 0 0 1.6rem;
    &.underline{
        padding-bottom:1.0rem;
        border-bottom:1px solid $header-underline-color;
    }
}

h3 {
    font-size: 2.5rem;
    color: $color-h3;
    margin-bottom: 1.0rem;
    &.underline{
        padding-bottom:0.4rem;
        border-bottom:1px solid $header-underline-color;
    }
}

h4 {
    font-size: 2.0rem;
    color: $color-h4;
    margin-bottom: 1.0rem;
    &.underline{
        padding-bottom:0.4rem;
        border-bottom:1px solid $header-underline-color;
    }
}

h5 {
    font-size: 1.8rem;
    color: $color-h5;
    margin-bottom: 0.8rem;
    &.underline{
        padding-bottom:0.4rem;
        border-bottom:1px solid $header-underline-color;
    }
}

h6 {
    font-size: 1.7rem;
    font-weight: normal;
    color: $color-h6;
    margin-bottom: 0.5rem;
    margin-top: 0;
    &.underline{
        padding-bottom:0.4rem;
        border-bottom:1px solid $header-underline-color;
    }
}